    max-width: 12.5%; }
  .gallery-columns-9 .gallery-item {
    max-width: 11.11%; }

.gallery-caption {
  display: block; }

.bx-wrapper {
  overflow: hidden;
  position: relative; }
  .bx-wrapper .bx-controls {
    position: absolute;
    height: 100%;
    top: 0;
    width: 100%; }
    .bx-wrapper .bx-controls a.bx-prev, .bx-wrapper .bx-controls a.bx-next {
      font-size: 5em;
      color: white;
      position: absolute;
      height: 60%;
      top: 20%;
      z-index: 51;
      width: 10vh;
      display: flex;
      justify-content: center;
      align-items: center;
      text-decoration: none; }
      .bx-wrapper .bx-controls a.bx-prev:hover, .bx-wrapper .bx-controls a.bx-next:hover {
        background: rgba(0, 0, 0, 0.1); }
      .bx-wrapper .bx-controls a.bx-prev.bx-next, .bx-wrapper .bx-controls a.bx-next.bx-next {
        right: 0; }

section[rel="Startseite"] {
  position: relative; }

.custom-pager {
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 51;
  display: flex;
  border: 1px solid black; }
  .custom-pager a {
    display: block;
    background: #fff; }
    .custom-pager a:not(:last-child) {
      border-right: 1px solid #111; }
    .custom-pager a.active, .custom-pager a:hover {
      background-color: #111;
      color: #fff; }
  .custom-pager .post-format-icon:before {
    padding: 0.5rem;
    margin: 0; }

.slider figure {
  position: relative; }
  .slider figure.active-slide img {
    transform: scale(1.2); }
  .slider figure a {
    height: 100%; }
  .slider figure img {
    transition: 15s cubic-bezier(0.32, 0.31, 0.43, 0.84); }
  .slider figure figcaption h1, .slider figure figcaption h2, .slider figure figcaption .more-link {
    opacity: 0;
    transform: translate(-50px, 0); }
  .slider figure figcaption h1 {
    display: inline;
    transition: opacity .5s ease 0s, transform .5s ease 0s;
    background-color: #fff;
    color: #111;
    font-size: 4rem;
    padding: 0.8rem; }
    .slider figure figcaption h1:before {
      font-size: 2.5rem; }
  .slider figure figcaption h2 {
    margin-top: 20px;
    background: rgba(0, 0, 0, 0.9);
    color: white;
    transition: opacity .5s ease .5s, transform .5s ease .5s; }
  .slider figure figcaption .more-link {
    transition: opacity .5s ease .8s, transform .5s ease .8s; }
  .slider figure.active-slide h1, .slider figure.active-slide h2, .slider figure.active-slide .more-link {
    opacity: 1;
    transform: translate(0, 0); }

.grid figure {
  position: relative;
  overflow: hidden;
  background-color: #111; }
  .grid figure img {
    transition: transform .3s ease, opacity .3s ease; }
  .grid figure:hover img {
    transform: scale(1.1);
    opacity: 0.5; }
  .grid figure figcaption h1 {
    font-size: 1.5rem; }
  .grid figure figcaption h2 {
    display: none; }

section[rel="Über uns"] {
  text-align: center; }
  section[rel="Über uns"] i {
    font-size: 5rem; }

.slider .page-header h1.entry-title, .slider .page-header h1.page-title, .slider .entry-content h1.entry-title, .slider .entry-content h1.page-title, .single .page-header h1.entry-title, .single .page-header h1.page-title, .single .entry-content h1.entry-title, .single .entry-content h1.page-title, .archive .page-header h1.entry-title, .archive .page-header h1.page-title, .archive .entry-content h1.entry-title, .archive .entry-content h1.page-title {
  display: inline-block;
  background-color: black;
  padding: 0.3em;
  position: absolute;
  transform: translate(0, -4em);
  color: #fff; }

.slider figure, .single figure, .archive figure {
  height: 600px; }

.archive .page-header h1 {
  transform: translate(20px, -4em); }

